'use client';

import { useState } from 'react';
import useSWR from 'swr';
import Link from 'next/link';
import { Plus, MapPin, Bed } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';

const fetcher = async (url: string) => {
    const res = await fetch(url);
    const text = await res.text();
    try {
        const data = JSON.parse(text);
        if (Array.isArray(data)) {
            return data.map((service: any) => ({
                ...service,
                images: typeof service.images === 'string' ? JSON.parse(service.images) : service.images,
                features: typeof service.features === 'string' ? JSON.parse(service.features) : service.features,
                tags: typeof service.tags === 'string' ? JSON.parse(service.tags) : service.tags,
            }));
        }
        return data;
    } catch (e) {
        console.error(`JSON Parse Error for ${url}:`, text);
        throw e;
    }
};

export default function AccommodationsPage() {
    const { data: services, error, isLoading } = useSWR('/api/services?category=Accommodation', fetcher);

    if (error) return <div>Failed to load accommodations</div>;
    if (isLoading) return <div>Loading...</div>;

    return (
        <div className="flex-1 space-y-4 p-8 pt-6">
            <div className="flex items-center justify-between space-y-2">
                <div>
                    <h2 className="text-3xl font-bold tracking-tight flex items-center gap-2">
                        <Bed className="h-8 w-8" />
                        Accommodations
                    </h2>
                    <p className="text-muted-foreground">
                        Manage riads, hotels, and guest houses.
                    </p>
                </div>
                <Link href="/admin/services/new">
                    <Button>
                        <Plus className="mr-2 h-4 w-4" />
                        Add Accommodation
                    </Button>
                </Link>
            </div>

            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {services?.map((service: any) => (
                    <Card key={service.id} className="overflow-hidden hover:shadow-lg transition-shadow">
                        {service.images[0] && (
                            <div className="aspect-video relative overflow-hidden bg-muted">
                                {/* eslint-disable-next-line @next/next/no-img-element */}
                                <img
                                    src={service.images[0]}
                                    alt={service.title}
                                    className="object-cover w-full h-full"
                                />
                            </div>
                        )}
                        <CardHeader>
                            <CardTitle className="line-clamp-1">{service.title}</CardTitle>
                            <CardDescription className="flex items-center gap-2">
                                <MapPin className="h-3 w-3" />
                                {service.location || 'Marrakech'}
                            </CardDescription>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-2">
                                <div className="flex items-center justify-between">
                                    <span className="text-2xl font-bold">${service.price}</span>
                                    <span className="text-sm text-muted-foreground">per night</span>
                                </div>
                                {service.latitude && service.longitude && (
                                    <Badge variant="secondary" className="text-xs">
                                        üìç {service.latitude.toFixed(4)}, {service.longitude.toFixed(4)}
                                    </Badge>
                                )}
                                <Link href={`/admin/services/${service.id}`}>
                                    <Button variant="outline" className="w-full mt-2">
                                        Edit Details
                                    </Button>
                                </Link>
                            </div>
                        </CardContent>
                    </Card>
                ))}
                {services?.length === 0 && (
                    <Card className="col-span-full">
                        <CardContent className="flex flex-col items-center justify-center py-12">
                            <Bed className="h-12 w-12 text-muted-foreground mb-4" />
                            <p className="text-muted-foreground mb-4">No accommodations found.</p>
                            <Link href="/admin/services/new">
                                <Button>
                                    <Plus className="mr-2 h-4 w-4" />
                                    Add Your First Accommodation
                                </Button>
                            </Link>
                        </CardContent>
                    </Card>
                )}
            </div>
        </div>
    );
}
